.contacts {
  margin: 87px 0 39px 0;
  position: relative;
}

.contacts__wrapper {
  @include wrapper;
}

.contacts__title {
  @include font-text($OpenSans, 18px, 30px, 700);
  color: $black;
  margin: 0;
  margin-bottom: 26px;
}

.contacts__email a:hover {
  color: $svg-hover;
}

.contacts__email a:active {
  opacity: 0.3;
}

.contacts__block {
  display: flex;
  flex-direction: column;
  margin-bottom: 533px;
}

.contacts__email {
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;

  & p {
    @include font-text($OpenSans, 15px, 30px, 400);
    margin: 0;
  }

  & a {
    @include font-text($OpenSans, 15px, 30px, 400);
    color: $text;
    width: 178px;
    text-decoration: none;
    position: relative;
  }
}

.contacts__email a::after {
  content: "";
  position: absolute;
  display: block;
  top: 27px;
  left: 0;
  width: 97%;
  height: 1px;
  background-color: $greeny-blue;
}

.contacts__adress {
  display: flex;
  justify-content: space-between;

  & p {
    @include font-text($OpenSans, 15px, 30px, 400);
    margin: 0;
    text-align: left;
    max-width: 178px;
  }

  & span {
    @include font-text($OpenSans, 15px, 30px, 400);
    margin: 0;
    text-align: left;
    max-width: 145px;
    margin-right: 39px;
  }
}

.contacts__link {
  margin-top: 39px;
}


@media (min-width: $tablet-width) {
  .contacts {
    margin-top: 81px;
  }

  .contacts__block {
    flex-direction: row;
    margin-bottom: 568px;
  }

  .contacts__email p {
    @include font-text($OpenSans, 17px, 30px, 400);
    padding-right: 20px;
  }

  .contacts__email a {
    @include font-text($OpenSans, 17px, 30px, 400);
    width: auto;
  }

  .contacts__adress {
    margin-left: 55px;
  }

  .contacts__adress p {
    @include font-text($OpenSans, 17px, 30px, 400);
    padding-right: 35px;
  }

  .contacts__adress span {
    @include font-text($OpenSans, 17px, 30px, 400);
    max-width: 230px;
    margin: 0;
  }

  .contacts__title {
    @include font-text($OpenSans, 43px, 50px, 700);
    margin-bottom: 49px;
    margin-left: -5px;
  }

  .contacts__title::after{
  content: "";
  position: absolute;
  top:-15px;
  right: 55px;
  width: 47px;
  height: 81px;
  background-image: url("../../img/icon-flag.svg");
  }

  .contacts__link {
    @include font-text($OpenSans, 17px, 20px, 700);
    margin: 0 auto;
    max-width: 230px;
  }
}


@media (min-width: $desktop-width) {
  .contacts {
    margin-top: 175px;
    padding-bottom: 105px;
    margin-bottom: 0;
  }

  .contacts__title {
    @include font-text($OpenSans, 53px, 57px, 700);
  }

  .contacts__wrapper {
    padding-left: 225px;
    z-index: 1;
    position: relative;
  }

  .contacts__block {
    flex-direction: column;
    max-width: 300px;
    margin-bottom: 50px;
  }

  .contacts__email {
    margin-bottom: 30px;
  }

  .contacts__adress {
    margin: 0;
  }

  .contacts__link {
    margin: 0;
    max-width: 230px;
  }
}
