.main-intro__image {
  background: url("../../img/hero-mobile@1x.jpg") center -15px no-repeat;
  @media (min-resolution: $retina-dpi),
  (min-resolution: $retina-dppx) {
    background: url("../../img/hero-mobile@2x.jpg") center cover no-repeat;
  }
}

@media (min-width: $tablet-width) {
  .main-intro__image {
    background: url("../../img/hero-tablet@1x.jpg") center 1px no-repeat;
    height: 746px;

    @media (min-resolution: $retina-dpi),
    (min-resolution: $retina-dppx) {
      background: url("../../img/hero-tablet@2x.jpg") center cover no-repeat;
    }
  }
}

@media (min-width: $desktop-width) {
  .main-intro__image {
    height: 746px;
    background: url("../../img/hero-desktop@1x.jpg") center 1px no-repeat;

    @media (min-resolution: $retina-dpi),
    (min-resolution: $retina-dppx) {
      background: url("../../img/hero-desktop@2x.jpg") center cover no-repeat;
    }
  }
}
