.form__submit {
  @include wrapper;
  text-align: center;
}

.form__required {
  @include font-text($OpenSans, 14px, 16px, 400);
  display: block;
  color: $text;
  margin: 18px 0 31px 0;
}

.form__button {
  @include font-text($OpenSans, 17px, 16px, 700);
  max-width: 260px;
  width: 100%;
  background-color: $white-three;
}


@media (min-width: $tablet-width) {
  .form__button {
    max-width: 230px;
  }

  .form__submit {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: space-between;
  }

  .form__required {
    @include font-text($OpenSans, 17px, 30px, 400);
    margin: 0;
  }
}

@media (min-width: $desktop-width) {
  .form__submit {
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 50px 255px 50px 211px;
  }
}
